import React, { Component } from 'react';
import { useState, useEffect } from 'react';
import battery from '../public/battery.png';
import './clock.css';

//https://medium.com/programming-essentials/how-to-create-a-digital-clock-with-react-hooks-aa30f76cfe3f
//https://productoptimist.com/start-using-react-hooks-a-clock-timer-example/

export function Clock() {
  const [date, setDate] = useState(new Date());

 //Replaces componentDidMount and componentWillUnmount
 useEffect(() => {
  var timerID = setInterval( () => tick(), 1000 );

  return function cleanup() {
      clearInterval(timerID);
    };
 });

   function tick() {
    setDate(new Date());
   }
	
  return (
  
  <div className="timer" style={{color:'white'}}>
	Current Time : {date.toLocaleTimeString()}
  </div>
  );
}